.alert {
    display: flex;
    align-items: center;
    position: fixed !important;
    right: 0;
    bottom: 50px;
    border-radius: 4px;
    overflow: hidden;
    z-index: 999;
    min-width: 420px;
    height: 70px;
    padding: 20px 40px;

}

.alert.addColor {
    background-color: #00FF00;
    border-left: #00CD00;
}

.alert.deleteColor {
    background-color: #ff6e6e;
    border-left: #be1627;
}

.alert.editColor {
    background-color: #ffdb9b;
    border-left: 8px solid #ffa502;
}


.alert.show {
    animation: show_notice 1s ease forwards;
}

@keyframes show_notice {
    0% {
        transform: translateX(100%);
    }

    40% {
        transform: translateX(-10%);
    }

    80% {
        transform: translateX(0%);
    }

    100% {
        transform: translateX(-10px);
    }
}

.alert.hide {
    display: none;
}

.alertIcon {
    left: 20px;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    font-size: 30px;
}

.alertIcon.addColor {
    color: #008B45;
}

.alertIcon.deleteColor {
    color: #DC3545;
}

.alertIcon.editColor {
    color: #ffa502;
}


.alertMessage {
    padding-left: 50px;
    font-size: 18px;
}

.alertMessage.editColor {
    color: #ffa502;
}

.alertMessage.addColor {
    color: #008B45;
}

.alertMessage.deleteColor {
    color: #DC3545;
}

.alertCloseBtn {
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    padding: 20px 18px;
    cursor: pointer;
}

.alertCloseBtn.editColor {
    opacity: 0.7;
    background-color: #ffd080;
}

.alertCloseBtn.addColor {
    background-color: #00df00;
    opacity: 0.7;
}

.alertCloseBtn.deleteColor {
    opacity: 0.7;
    background-color: #be1627;
}

.alertCloseBtn:hover {
    opacity: 1;
}

.alertIconClose {
    font-size: 22px;
    line-height: 40px;
}

.alertIconClose.editColor {
    color: rgb(240, 240, 240);
}

.alertIconClose.addColor {
    color: rgb(240, 240, 240);
}

.alertIconClose.deleteColor {
    color: rgb(240, 240, 240);
}